import MainLayout from "@/layouts/main";
import { Button, Modal, ModalContent } from "@heroui/react";
import dynamic from "next/dynamic";
import { useState } from "react";

const FormTopic = dynamic(() => import("@/components/views/support/newform"), {
  ssr: false,
});

const TicketsList = dynamic(() => import("@/components/views/support/list"), {
  ssr: false,
});

const SupportView = () => {
  const [isOpen, setOpen] = useState(false);

  return (
    <MainLayout>
      <Button onPress={() => setOpen(true)}>Abrir nuevo ticket</Button>
      <Modal isOpen={isOpen} onClose={() => setOpen(false)}>
        <ModalContent className="pt-8">
          <FormTopic onClose={() => {}} />
        </ModalContent>
      </Modal>

      <div className="card mt-8">
        <TicketsList />
      </div>
    </MainLayout>
  );
};

export default SupportView;
